import { Component, OnInit } from '@angular/core';
import { ApprovedModel, CheckTaskModel, ProcessDefinitionTypeModel } from '../process-instance-model';
import { paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, handleResult, formatDate, showMessage } from '../../common/func/common-func';
import { CheckTaskQueryService } from './check-task-query.service';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { TASK_NODE_TYPES, TASK_URGENT, TASK_DO_TYPES, TASK_TYPES, TASK_RATE, MANAGE_TASKS_TYPES, CHECK_TASK_STATES, PROCESS_ISTIMEOUT } from '../../common/constant/code.value';
import { ProcessInstanceManagementService } from '../process-instance-management/process-instance-management.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'app-check-task-query',
  templateUrl: './check-task-query.component.html',
  styleUrls: ['./check-task-query.component.scss']
})
export class CheckTaskQueryComponent implements OnInit {

  msgs = [];
  // 
  toDoTasks: CheckTaskModel[] = [];
  // 
  queryToDoTask: CheckTaskModel = new CheckTaskModel();
  // 
  paginatorInstance: CheckTaskModel = new CheckTaskModel();
  // 
  selectedToDoTask: CheckTaskModel = new CheckTaskModel();
  // 
  processVerInfoDisplay = false;
  // 
  queryApproveds: ApprovedModel[] = [];
  // 
  processDefinition: ProcessDefinitionTypeModel = null;
  // 
  total: string;
  // 
  loginSys: string = '';
  // 
  queryStartTimeStart: Date;
  // 
  queryStartTimeEnd: Date;
  // 
  queryEndTimeStart: Date;
  // 
  queryEndTimeEnd: Date;

  CALENDAR_CN = CALENDAR_CN;
  // 
  queryUnstartedTask: DictionaryTypeModel[] = [];
  // 
  selectedUnstartedTask: DictionaryTypeModel = new DictionaryTypeModel();
  // 
  degreeOfEmergency = TASK_URGENT;
  // 
  taskDoStateArray = TASK_DO_TYPES;
  // 
  businessNodeTypes = TASK_NODE_TYPES;
  // 
  selectedQueryTaskType: DictionaryTypeModel = new DictionaryTypeModel();
  // 
  queryTaskStatus = CHECK_TASK_STATES;
  // 
  selectedQueryTaskStatus: DictionaryTypeModel = new DictionaryTypeModel();
  // 
  taskTypes = TASK_TYPES;
  // 
  taskRates = TASK_RATE;
  dialogParam = {
    display: false,
    headerTitle: ''
  };

  // 
  // items: MenuItem[];
  // 
  manageType = MANAGE_TASKS_TYPES;
  // 
  taskType: string;
  // 
  isRest: boolean;

  // 
  isTimeOuts = PROCESS_ISTIMEOUT;
  // 
  querySelectedIsTimeOut: DictionaryTypeModel = new DictionaryTypeModel();

  constructor(
    public commfunc: Commfunc,
    private messageService: MessageService,
    private checkTaskQueryService: CheckTaskQueryService,
    private processInstanceManagementService: ProcessInstanceManagementService,
  ) {

    this.queryUnstartedTask = [
      { type: '', descp: '' },
      { type: '0', descp: '' },
      { type: '1', descp: '' }
    ];

  }

  ngOnInit() {
    this.loginSys = sessionStorage.getItem('sys');
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;

    // this.items = [
    // {
    //   label: '', icon: 'fa ui-icon-edit', command: (event) => {
    //     this.taskModalShow(0);
    //   }
    // },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(3);
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(2);
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(5);
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(1);
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(6);
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.recoveryHangTask();
    //     }
    //   },
    //   {
    //     label: '', command: (event) => {
    //       this.taskModalShow(7);
    //     }
    //   },
    // ];
  }

  // 
  reset(dt): void {
    this.queryToDoTask.processName = null;
    this.queryToDoTask.processId = null;
    this.queryToDoTask.taskName = null;
    this.queryToDoTask.doTaskActor = null;
    this.queryToDoTask.taskId = null;
    this.selectedQueryTaskType = new DictionaryTypeModel();
    this.selectedQueryTaskStatus = new DictionaryTypeModel();
    this.querySelectedIsTimeOut = new DictionaryTypeModel();
    this.queryStartTimeStart = null;
    this.queryStartTimeEnd = null;
    this.queryEndTimeStart = null;
    this.queryEndTimeEnd = null;
    this.queryFirst(dt);
  }

  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 
    this.paginatorInstance.rowNum = table.rows + '';
    // 
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getToDoTasksList(this.queryToDoTask.processName, this.queryToDoTask.processId, this.selectedQueryTaskType.type, this.queryToDoTask.taskName, this.queryToDoTask.doTaskActor, this.selectedQueryTaskStatus.type, this.queryStartTimeStart, this.queryStartTimeEnd, this.queryEndTimeStart, this.queryEndTimeEnd, this.querySelectedIsTimeOut.type, this.queryToDoTask.taskId);
  }


  // 
  getToDoTasksList(processName?: string, processId?: string, nodeType?: string, nodeName?: string, doTaskActor?: string, workState?: string, queryStartTimeStart?: any, queryStartTimeEnd?: any, queryEndTimeStart?: any, queryEndTimeEnd?: any, isTimeout?: string, taskId?: string): void {
    const workStates = !workState ? ['0', '2'] : [`${workState}`];
    const createBeginTime = queryStartTimeStart == null ? null : formatDate(queryStartTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const startBeginTime = queryStartTimeStart == null ? null : formatDate(queryStartTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const createEndTime = queryStartTimeEnd == null ? null : formatDate(queryStartTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const startEndTime = queryStartTimeEnd == null ? null : formatDate(queryStartTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const closeBeginTime = queryEndTimeStart == null ? null : formatDate(queryEndTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const closeEndTime = queryEndTimeEnd == null ? null : formatDate(queryEndTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const bodyParams = {
      processName,
      processId,
      nodeType,
      nodeName,
      doTaskActor,
      workStates,
      workState,
      startBeginTime,
      startEndTime,
      closeBeginTime,
      closeEndTime,
      isTimeout,
      taskId,
      // userId: this.loginSys,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.checkTaskQueryService.getCheckTasks(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.toDoTasks = data.pageData[0].result;
            this.toDoTasks && this.toDoTasks.length && this.toDoTasks.map(item => {
              item.customSystemCode = item.processCode.split(":")[0];
              item.customWorkCreater = item.workCreater ? item.workCreaterName ? `${item.workCreater} (${item.workCreaterName})` : `${item.workCreater}` : '';
            });
            this.total = data.pageData[0].totalRows;
          });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }



  // 
  taskModalShow(index: number): void {
    if (this.selectedToDoTask && (this.selectedToDoTask.workState === '2')) {
      showMessage(this.messageService, 'error', ',');
      return;
    }
    this.isRest = !this.isRest;
    this.dialogParam.headerTitle = this.manageType[index].descp;
    this.taskType = this.manageType[index].type;
    this.dialogParam.display = true;
  }
  // 
  callBack(event): void {
    this.dialogParam.display = !event;
    this.getToDoTasksList(this.queryToDoTask.processName, this.queryToDoTask.processId, this.selectedQueryTaskType.type, this.queryToDoTask.taskName, this.queryToDoTask.doTaskActor, this.selectedQueryTaskStatus.type, this.queryStartTimeStart, this.queryStartTimeEnd, this.queryEndTimeStart, this.queryEndTimeEnd, this.querySelectedIsTimeOut.type);
  }

  // 
  recoveryHangTask(): void {
    if (this.selectedToDoTask && (this.selectedToDoTask.workState !== '2')) {
      showMessage(this.messageService, 'error', '');
      return;
    }
    const bodyParams = {
      userId: this.loginSys,
      processId: this.selectedToDoTask.processId,
      taskId: this.selectedToDoTask.taskId,
      tranType: 'M4'
    };
    this.processInstanceManagementService.taskManage(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getToDoTasksList(this.queryToDoTask.processName, this.queryToDoTask.processId, this.selectedQueryTaskType.type, this.queryToDoTask.taskName, this.queryToDoTask.doTaskActor, this.selectedQueryTaskStatus.type, this.queryStartTimeStart, this.queryStartTimeEnd, this.queryEndTimeStart, this.queryEndTimeEnd, this.querySelectedIsTimeOut.type);
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      )
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }


}
