import { Component, OnInit } from '@angular/core';
import { EngTypeModel, SingleExceptionInformationModel, ExceptionInformationModel } from '../../process-instance/process-instance-model';
import { MessageService } from 'primeng/components/common/messageservice';
import { ManualExceptionInformationService } from './manual-exception-information.service';
import { CALENDAR_CN, paginatorUtils } from '../../common/constant/config';
import { handleError, handleResult, showMessage } from '../../common/func/common-func';
import { EXCEPTION_TYPE, EXCEPTION_PATTERN, EXCEPTION_HANDLE, EXCEPTION_STATUS, TASK_NODE_TYPES, EXP_TYPE } from '../../common/constant/code.value';

@Component({
  selector: 'app-manual-exception-information',
  templateUrl: './manual-exception-information.component.html',
  styleUrls: ['./manual-exception-information.component.scss']
})
export class ManualExceptionInformationComponent implements OnInit {

  msgs = [];
  // 
  exceptionTypes = EXCEPTION_TYPE;
  // 
  queryExceptionType: EngTypeModel = new EngTypeModel();
  // 
  exceptionPatterns = EXCEPTION_PATTERN;
  // 
  exceptionStatus = EXCEPTION_STATUS;
  // 
  queryExceptionStatus: EngTypeModel = new EngTypeModel();
  // 
  queryExceptionPattern: EngTypeModel = new EngTypeModel();
  // 
  queryNodeTypes = TASK_NODE_TYPES;
  // 
  selectedNodeType: EngTypeModel = new EngTypeModel();
  // 
  exceptionInformations: ExceptionInformationModel[] = [];
  // 
  selectedExceptionInformation: ExceptionInformationModel = new ExceptionInformationModel();
  //
  queryExceptionInstance: ExceptionInformationModel = new ExceptionInformationModel();
  // 
  paginatorInstance: ExceptionInformationModel = new ExceptionInformationModel();

  CALENDAR_CN = CALENDAR_CN;
  // 
  startDate: string;
  // 
  endDate: string;
  // 
  total: string;
  // 
  handleStatus = EXCEPTION_HANDLE;
  // ID
  singExceptionInformation: SingleExceptionInformationModel = new SingleExceptionInformationModel();

  dialogParam = {
    display: false,
    headerTitle: ''
  };
  // 
  exceptionMessage: string;
  // 
  viewExceptionDisplay: boolean = false;
  // 
  expTypeOption = EXP_TYPE;
  // 
  expType: EngTypeModel = new EngTypeModel();
  constructor(
    private messageService: MessageService,
    private manualExceptionInformationService: ManualExceptionInformationService
  ) {
  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;

  }

  // 
  getExceptionList(expProcCode?: string, expProcId?: string, expTaskId?: string, expNodeType?: string, expNodeName?: string, startTime?: string, endTime?: string, expDoFlag?: string, expType?: string): void {
    const bodyParams = {
      expProcCode,
      expProcId,
      expTaskId,
      expNodeType,
      expNodeName,
      startTime,
      endTime,
      expDoFlag,
      expType,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.manualExceptionInformationService.getExceptionList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.exceptionInformations = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
          this.exceptionInformations && this.exceptionInformations.length && this.exceptionInformations.map(item => {
            item.customSystemCode = item.expProcCode.split(":")[0];
          });
        }, true, { errorMsg: '' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }


  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 
    this.paginatorInstance.rowNum = table.rows + '';
    // 
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getExceptionList(this.queryExceptionInstance.expProcCode, this.queryExceptionInstance.expProcId, this.queryExceptionInstance.expTaskId, this.selectedNodeType.type, this.queryExceptionInstance.expNodeName, this.startDate, this.endDate, this.queryExceptionStatus.type, this.expType.type);
  }


  // 
  reset(dt): void {
    this.queryExceptionStatus = new EngTypeModel();
    this.selectedNodeType = new EngTypeModel();
    this.queryExceptionInstance = new ExceptionInformationModel();
    this.startDate = null;
    this.endDate = null;
    this.expType = new EngTypeModel();
    this.queryFirst(dt);
  }

  // 
  dealException(item): void {
    const bodyParams = {
      id: item.id
    };
    this.manualExceptionInformationService.dealExpDo(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.getExceptionList();
        }, true, { message: ' ', errorMsg: '' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

  // 
  showEventService(eventService, item): void {
    eventService.getEventServiceDetail(item.evtServiceId)
    this.dialogParam.headerTitle = `ID${item.evtServiceId}`
    this.dialogParam.display = true;
  }


  callBack(event): void {
    this.dialogParam.display = !event;
  }


  // 
  ignoreFunc(item, table): void {
    console.log(item)
    if (item.expDoFlag === '2') {
      showMessage(this.messageService, 'error', '');
      return;
    }
    const bodyParams = {
      id: item.id + '',
      expDoFlag: '2'
    };
    this.manualExceptionInformationService.updateExceptionData(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.query(table);
        }, true, { message: '', errorMsg: '' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }


  // 
  showManualInfo(item): void {
    this.exceptionMessage = item.expMessage;
    this.viewExceptionDisplay = true;
  }
  // 
  toActivitiPage(item) {
    this.manualExceptionInformationService.getShowProcessUrl({processId:item.expProcId})
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          if(data.pageData[0].urlAddr){
            const urlAddr = data.pageData[0].urlAddr
            const url = urlAddr.slice(urlAddr.indexOf('workflow-app'));
            console.log(url);
            this.manualExceptionInformationService.toActivitiSchedule(url);
          }else {
            showMessage(this.messageService, 'error', 'URL!');
          }
        }, true, { errorMsg: 'URL' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }
}
